import { BaseProduct } from '../types/scraper';
import { SortField, SortDirection } from '../hooks/useSortedFoils';

export function sortProducts(
  products: BaseProduct[],
  field: SortField,
  direction: SortDirection
): BaseProduct[] {
  return [...products].sort((a, b) => {
    let valueA: string | number;
    let valueB: string | number;

    if (field === 'brand' || field === 'series' || field === 'model' || field === 'type') {
      valueA = a[field].toLowerCase();
      valueB = b[field].toLowerCase();
    } else if (field === 'price') {
      valueA = a.price || 0;
      valueB = b.price || 0;
    } else if (field in a.specs) {
      valueA = a.specs[field as keyof typeof a.specs] || 0;
      valueB = b.specs[field as keyof typeof b.specs] || 0;
    } else {
      valueA = '';
      valueB = '';
    }
    
    const multiplier = direction === 'asc' ? 1 : -1;
    
    if (typeof valueA === 'string' && typeof valueB === 'string') {
      return valueA.localeCompare(valueB) * multiplier;
    }
    if (typeof valueA === 'number' && typeof valueB === 'number') {
      return (valueA - valueB) * multiplier;
    }
    return 0;
  });
} 