import { useState, useCallback } from 'react';
import { BaseProduct } from '../types/scraper';

export function useComparison() {
  const [selectedFoils, setSelectedFoils] = useState<BaseProduct[]>([]);

  const toggleFoilSelection = useCallback((foil: BaseProduct) => {
    setSelectedFoils(prev => {
      const isAlreadySelected = prev.some(f => f.model === foil.model && f.brand === foil.brand);
      if (isAlreadySelected) {
        return prev.filter(f => !(f.model === foil.model && f.brand === foil.brand));
      }
      return [...prev, foil];
    });
  }, []);

  const isSelected = useCallback((foil: BaseProduct) => {
    return selectedFoils.some(f => f.model === foil.model && f.brand === foil.brand);
  }, [selectedFoils]);

  const clearSelection = useCallback(() => {
    setSelectedFoils([]);
  }, []);

  return {
    selectedFoils,
    toggleFoilSelection,
    isSelected,
    clearSelection,
  };
} 