import { useState, useMemo } from 'react';
import { BaseProduct } from '../types/scraper';
import { FilterOptions } from '../types/filters';

export function useFilteredFoils(foils: BaseProduct[]) {
  const [filters, setFilters] = useState<FilterOptions>({
    brands: [],
    series: [],
    type: null,
  });

  const filterOptions = useMemo(() => {
    const brands = Array.from(new Set(foils.map(foil => foil.brand)));
    const allSeries = Array.from(new Set(foils.map(foil => foil.series)));
    const types = ['front', 'tail'];

    // Create brandSeries mapping
    const brandSeries = brands.reduce((acc, brand) => {
      acc[brand] = Array.from(new Set(
        foils
          .filter(foil => foil.brand === brand)
          .map(foil => foil.series)
      ));
      return acc;
    }, {} as Record<string, string[]>);

    // Filter foils based on selected type
    const typeFilteredFoils = filters.type 
      ? foils.filter(foil => foil.type === filters.type)
      : foils;

    // Get series based on type-filtered foils
    const typeFilteredSeries = Array.from(new Set(typeFilteredFoils.map(foil => foil.series)));

    // Filter series based on selected brands and type
    const series = filters.brands.length > 0
      ? Array.from(new Set(
          typeFilteredFoils
            .filter(foil => filters.brands.includes(foil.brand))
            .map(foil => foil.series)
        ))
      : typeFilteredSeries;

    return {
      brands,
      series,
      types,
      brandSeries,
    };
  }, [foils, filters.brands, filters.type]);

  const filteredFoils = useMemo(() => {
    return foils.filter(foil => {
      const brandMatch = filters.brands.length === 0 || filters.brands.includes(foil.brand);
      const seriesMatch = filters.series.length === 0 || filters.series.includes(foil.series);
      const typeMatch = !filters.type || foil.type === filters.type;

      return brandMatch && seriesMatch && typeMatch;
    });
  }, [foils, filters]);

  return {
    filters,
    setFilters,
    filteredFoils,
    filterOptions,
  };
} 