import { useMemo } from 'react';
import { BaseProduct } from '../types/scraper';

export function useSearchFoils(foils: BaseProduct[], searchQuery: string) {
  const searchedFoils = useMemo(() => {
    if (!searchQuery.trim()) {
      return foils;
    }

    const query = searchQuery.toLowerCase().trim();
    return foils.filter(foil => {
      const searchableFields = [
        foil.brand,
        foil.model,
        foil.series,
        foil.specs.area.toString(),
        foil.specs.wingspan.toString(),
        foil.specs.aspectRatio.toString(),
      ];

      return searchableFields.some(field => 
        field.toLowerCase().includes(query)
      );
    });
  }, [foils, searchQuery]);

  return { searchedFoils };
} 