import { useState, useEffect } from 'react';
import { BaseProduct } from '../types/scraper';
import { ProductService } from '../services/productService';

export function useFoils() {
  const [foils, setFoils] = useState<BaseProduct[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const loadFoils = async () => {
    try {
      setLoading(true);
      setError(null);
      const products = await ProductService.getAllProducts();
      setFoils(products);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load foils');
      console.error('Error loading foils:', err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadFoils();
  }, []);

  const refreshFoils = () => {
    loadFoils();
  };

  return {
    foils,
    loading,
    error,
    refreshFoils
  };
} 